.box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn {
  width: 12em;
  height: 90px;
  color: #eceff4;
  text-transform: uppercase;
  cursor: pointer;
  background-color: rgba(33, 150, 243, .8);
  border: 0;
  border-radius: 80px;
  margin: 0 20px;
  font-size: 20px;
  box-shadow: 12px 12px 1px 1px rgba(76, 86, 106, .2);
}

.btn:disabled {
  cursor: not-allowed;
  background-color: gray;
}

